:root {
  --main-text: black;
  --secondry-text: #828282;
  --primary-text: #090909;
}

*,
*::after,
*::before {
  box-sizing: border-box;
}

img {
  display: block;
  max-width: 100%;
}

body {
  font-family: 'gilroy-bold';
}

p {
  font-family: 'Gilroy-Medium';
  font-size: 18px;
  line-height: 22px;
  color: var(--primary-text);
  text-align: center;
  margin: 0;
}

h2 {
  font-size: 34px;
  line-height: 42px;
  text-align: center;
  color: var(--main-text);
}

h3 {
  font-family: Gilroy-Bold;
  font-size: 14px;
  line-height: 17px;
  color: var(--secondry-text);
}

:root {
  --content-width: 944px;
  --gutter: 15px;
  --mob-padding: 29px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

@font-face {
  font-family: 'Gilroy-bold';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Gilroy-Bold.ttf");
}

@font-face {
  font-family: 'Gilroy-Medium';
  font-style: normal;
  font-weight: normal;
  src: url("../fonts/Gilroy-Medium.ttf");
}

.burger {
  z-index: 4;
  width: 33px;
  height: 20px;
  display: block;
}

.burger_btn {
  position: absolute;
  width: 30px;
  height: 4px;
  top: 50%;
  left: 4%;
  background-color: var(--main-text);
}

.burger_btn::before,
.burger_btn::after {
  width: 30px;
  height: 4px;
  content: '';
  display: block;
  background-color: var(--main-text);
  transition: 0.3s;
}

.burger_btn::before {
  transform: translateY(-6px);
}

.burger_btn::after {
  transform: translateY(2px);
}

.burger_active span {
  height: 0px;
}

.burger_active span::before {
  width: 28px;
  transform: rotate(45deg);
  background-color: var(--secondry-text);
}

.burger_active span::after {
  width: 28px;
  transform: rotate(-45deg);
  margin-top: -4px;
  background-color: var(--secondry-text);
}

.rate__star {
  content: url(../img/skills/eStar.svg);
  margin-right: 9px;
}

.rate__star:last-child {
  margin-right: 0;
}

.rate[data-mark="5"] .rate__star:nth-child(-n+5),
.rate[data-mark="4"] .rate__star:nth-child(-n+4),
.rate[data-mark="3"] .rate__star:nth-child(-n+3),
.rate[data-mark="2"] .rate__star:nth-child(-n+2),
.rate[data-mark="1"] .rate__star:nth-child(-n+1) {
  content: url(../img/skills/fStar.svg);
}

.btn {
  background: #070707;
  border-radius: 60px;
  font-family: Gilroy-Bold;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: white;
  border-width: 0;
  padding: 13px 0px;
  cursor: pointer;
}

.btn:hover {
  color: var(--main-text);
  background-color: white;
  transition: 0.3s;
  border-width: 1px;
  border-color: black;
}

.footer {
  padding-top: 43px;
  padding-bottom: 123px;
}

.footer__social-wraper {
  display: flex;
  justify-content: center;
  margin-bottom: 46px;
}

.footer__social-link {
  padding-right: 52px;
}

.footer__social-link:last-child {
  padding: 0;
}

.footer__text {
  max-width: 315px;
  margin: 0 auto;
  color: var(--secondry-text);
}

.header {
  padding-top: 65px;
}

.header__menu-wrap {
  position: fixed;
  display: none;
}

.header__menu-wrap_visible {
  display: block;
  background-color: white;
  width: 100%;
  transition: 1s;
  z-index: 3;
  left: 0;
}

.header .menu-item {
  list-style-type: none;
  font-size: 34px;
  line-height: 42px;
  padding-top: 65px;
}

.header .menu-item:first-child {
  padding-top: 0;
  color: var(--main-text);
}

.header .menu-item__link {
  text-decoration: none;
  color: var(--secondry-text);
}

.header .menu-item__link:hover {
  color: var(--main-text);
}

.header__burger {
  position: fixed;
  top: 9.8%;
  right: 7%;
}

.promo {
  padding-bottom: 83px;
}

.promo__title {
  max-width: 124px;
  max-height: 110px;
  width: 100%;
  height: 100%;
  font-size: 47px;
  line-height: 58px;
  margin-top: 8px;
}

.promo__text {
  width: 155px;
  height: 44px;
  margin-bottom: 37px;
}

.promo__leng-wrap {
  display: none;
}

.promo__leng-switch {
  cursor: pointer;
}

.promo__leng-switch>span {
  color: var(--main-text);
}

.promo__self-introduction {
  background-image: url(../img/promo/promo3.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top;
  width: 100%;
  display: block;
  height: 478px;
  position: relative;
}

.about {
  padding-top: 81px;
  padding-bottom: 76px;
}

.about__title {
  margin: 0;
  padding-bottom: 9px;
}

.about__text-wraper {
  max-width: 316px;
  max-height: 261px;
  margin: 0;
  padding-top: 9px;
  margin: 0 auto;
}

.about__text {
  padding-bottom: 20px;
  width: 100%;
}

.skills {
  padding-top: 85px;
  padding-bottom: 85px;
}

.skills__title {
  padding-bottom: 50px;
}

.skills__description {
  padding-bottom: 71px;
  display: block;
  font-family: Gilroy-Medium;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
}

.skills__icons-wraper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.skills__skill-card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-bottom: 80px;
}

.skills__skill-card:last-child {
  margin-bottom: 0;
}

.skills__ikon {
  margin-bottom: 25px;
}

.skills__ikon_has-bg {
  width: 89px;
  height: 89px;
  background: radial-gradient(75.99% 75.64% at 32.94% 22.44%, rgba(52,54,78,0.98) 0%, #0C0824 100%);
}

.skills__card-name {
  margin-bottom: 40px;
  max-width: 79px;
  max-height: 27px;
  text-align: center;
}

.portfolio {
  padding-top: 98px;
}

.portfolio__title {
  margin-bottom: 65px;
  margin-top: 0;
}

.portfolio__image {
  margin-bottom: 45px;
}

.portfolio__link {
  display: block;
  text-align: center;
  margin-bottom: 100px;
  font-family: Gilroy-Medium;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  -webkit-text-decoration-line: underline;
  text-decoration-line: underline;
  color: var(--main-txt);
}

.contacts {
  padding-top: 114px;
  padding-bottom: 43px;
}

.contacts__button {
  display: block;
  width: 100%;
}

.contacts__text {
  display: block;
  max-width: 275px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 30px;
}

@media (min-width: 640px) {
  .burger {
    display: none;
  }

  .footer__social-link {
    padding-right: 60px;
  }

  .footer__social-link:last-child {
    padding: 0;
  }

  .header {
    padding-top: 0px;
    padding-bottom: 20px;
  }

  .header__menu-wrap {
    display: block;
    position: relative;
    top: 0;
    width: 100%;
    border-bottom: 1px solid #828282;
  }

  .header__menu-list {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding: 0;
  }

  .header .menu-item {
    padding: 32px 0px;
    font-size: 18px;
    line-height: 22px;
  }

  .header .menu-item:first-child {
    padding: 32px 0px;
  }

  .promo {
    padding-top: 20px;
  }

  .promo__text-wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 50px;
  }

  .promo__title {
    margin: 0;
  }

  .promo__text {
    margin: 0;
  }

  .promo__leng-wrap {
    display: block;
    position: relative;
    width: 21px;
    height: 65px;
  }

  .promo__leng-switch {
    font-family: 'Gilroy-Bold';
    font-size: 16px;
    line-height: 20px;
    transform: rotate(270deg);
    color: var(--secondry-text);
    padding-left: 15px;
    margin: 0;
    position: absolute;
    width: 80px;
    height: 21px;
    bottom: 30%;
    right: -150%;
  }

  .skills__skill-card {
    width: 50%;
    margin-bottom: 80px;
  }

  .skills__skill-card:last-child {
    margin-bottom: 80px;
  }

  .contacts__button {
    max-width: 180px;
    margin: 0 auto;
  }
}

@media (min-width: 1200px) {
  .about__text-wraper {
    max-width: 485px;
    max-height: 218px;
  }

  .skills {
    padding-bottom: 110px;
  }

  .skills__skill-card {
    width: 25%;
  }

  .portfolio {
    padding-top: 112px;
  }
}